@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;700&family=Poppins:ital,wght@0,700;0,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* 
Main Background: #F0F3F4
Swoosh Background: #EBEBEB
Text & Newsletter Background: #1C1C1C
Titles & Footer Background: #D1898B
Highlight Text: #EEDACB
*/
h1,h2,h3,h4,h6{
  color: #D1898B; 
  font-family: 'Poppins', sans-serif;
  font-weight: 900;
}
  h2{
    font-size: 2rem;
  }

p{
  color: rgb(163, 149, 149);
  font-family: 'Open Sans', sans-serif;
}

button{
    box-shadow: none;
    border: none;
    font-family: 'Open Sans', sans-serif;
    outline: double;
    padding: 10px;
}







/* Section 1 */
.section1 {
  
    display: flex;
      flex-direction: column;
      align-items: center;
    /* padding-bottom: 100px; */
    min-height: 50vh;
    position: relative;
    text-align: center;
    
  }
    .gradientSection{
      background: rgb(0,0,0);
      background: linear-gradient(90deg, rgb(0 0 0 / 80%) 0%, rgb(0 0 0 / 80%) 53%);
      height: 100%;
      /* outline: 1px solid red; */
      position: absolute;
      width: 100%;
      z-index: 0 !important;
    }
  .section1 > *{z-index: 10;}
  
  #section1ImageWrapper { 
    display: flex;
    flex-direction: column;
    align-items: center;
    /* height: 200px;  */
    /* padding-top: 50px; */
  }
  #coleSkateboard{
    height: 160px; 
    margin-bottom: 20px;}
    
  #skateType{
    height: 60px;
  }
  
  .headerWrapper{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 1;
  }
  .headerWrapper > p{
    color: #EEDACB;
    font-size: 16px;
    margin: 0 auto;
    margin-bottom: 40px;
    /* width: 60%; */
  }
  .headerWrapper > button{
    background-color: #ffffff00;
    color: #EEDACB;
    cursor: pointer;
    outline-color:#D1898B;
    position: relative; 
    z-index: 10; 
    
  }
  
  .headerWrapper > button:hover{
    -webkit-animation: headerWrapperButtonHover 1s;
            animation: headerWrapperButtonHover 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards; 
    color:white;
   }
   @-webkit-keyframes headerWrapperButtonHover {
     0%{background-color: #ffffff00;}
     50%{background-color: #D1898B;}
     100%{background-color: #D1898B;}
   }
   @keyframes headerWrapperButtonHover {
     0%{background-color: #ffffff00;}
     50%{background-color: #D1898B;}
     100%{background-color: #D1898B;}
   }

   /*  Menu */

   nav{
     background-color: rgba(255, 255, 255, 0);
     display: flex;
      justify-content: space-between;
     height: auto;
     /* outline: 1px solid white; */
     position: fixed;
     top: 0;
     width: 100%;
   }
   .navbar{
     background-color: #ffffff00;
     color: #D1898B;
    cursor: pointer; 
    padding: 30px;
     
   }

    .logo{height: 30px;}

   .menu{
     background-color: #1C1C1C;
     color: #EEDACB;
     display: flex;
      flex-direction: column;
      justify-content: space-between;
     /* outline: 1px solid red; */
     position: fixed;
     min-height: 100%;
     right: -10000px;
     text-transform: uppercase;
     top: 0px;
     width: 100%;
     /* max-width: 500px; */
     z-index: 100;
   }
   .openMenu{
     box-shadow:0px 10px 15px 0px #D1898B;
    right: 0;
    -webkit-animation: animateMenu 1s;
            animation: animateMenu 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
   }
   .websiteLogo, .toggleMenuButton{
    position: relative; 
    z-index: 10;
   }
   #menuBackground{position: fixed;}
   .menuBackground{
    background-color: #1c1c1cb8;
    height: 100%;

    position: fixed; 
    width: 100%;
    z-index: 5;
     

   }
   @-webkit-keyframes animateMenu {
    0%{
      right: -1000px;
    }

    50%{
      right: 10px;
    }
    100%{
      right: 0px;
    }
  }
   @keyframes animateMenu {
    0%{
      right: -1000px;
    }

    50%{
      right: 10px;
    }
    100%{
      right: 0px;
    }
  }
  
  .closeMenu{
    right: -100px;
    -webkit-animation: aniCloseMenu 1s;
            animation: aniCloseMenu 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  }

  @-webkit-keyframes aniCloseMenu {
    0%{
      right: 10px;
    }
    50%{
      right: -1000px;
    }
    100%{
      right: -1000px;
    }
  }

  @keyframes aniCloseMenu {
    0%{
      right: 10px;
    }
    50%{
      right: -1000px;
    }
    100%{
      right: -1000px;
    }
  }
  .stop-scrolling {
    height: 100%;
    overflow: hidden;
  }

  #menuHeader{
      display: flex;
      justify-content: space-between;
      padding-left: 40px;
      padding-right: 40px;
      padding-top: 25px;
  }
    #menuHeader > span{
      cursor: pointer;
    }
    .toggleMenuButton span{
      font-size: 30px;
    }
  #menuItems{
    text-align: left;
  }
    #menuItems ul{
      list-style-type: none;
      text-align: left;
    }
      #menuItems ul li{
        font-family: 'Poppins', sans-serif;
        /* font-size: 28px; */
        font-size: 2rem;
        padding-bottom: 10px;
        padding-top: 10px;
      }
        #menuItems ul li:hover{
          color: #D1898B;
          cursor: pointer;
          text-decoration: underline;
        }

#menuFooter{
      display: flex;
      justify-content: space-between;
      padding-left: 40px;
      padding-right: 40px;
      padding-bottom: 25px;
}
  #menuFooter > span{
    cursor: pointer;
    color: #D1898B;
  }
  #menuFooter > span:hover{
    text-decoration: underline;
  }

@media (min-width: 768px) {
  .menu{
    max-width: 500px;
  }
}
/************************************ Section 2 */
.section2{
    /* outline: 1px solid red; */
    margin: 0 auto;
    max-width: 1200px;
    padding-top: 70px;
    padding-bottom: 70px;
    -webkit-animation: animateProduct 3s;
            animation: animateProduct 3s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  }

  .animateTest{
    -webkit-animation: animateSectionTest 5s;
            animation: animateSectionTest 5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  }
  @-webkit-keyframes animateSectionTest {
    0%{
      /* background-color: red; */
      -webkit-transform: translateY(250px);
              transform: translateY(250px);
    }
    100%{
      /* background-color: yellow; */
      -webkit-transform: translateY(0px);
              transform: translateY(0px);
    }
  }
  @keyframes animateSectionTest {
    0%{
      /* background-color: red; */
      -webkit-transform: translateY(250px);
              transform: translateY(250px);
    }
    100%{
      /* background-color: yellow; */
      -webkit-transform: translateY(0px);
              transform: translateY(0px);
    }
  }
  .section2 > p{
    color: black ; 
    margin: 0 ;
    padding: 0; 
  }
  
  .section2 > h2{
    margin: 0; 
      margin-top: 10px; 
      margin-bottom:30px ;
      
    padding: 0; 
  
  }
  
  .imageRow{
    display: flex;
      flex-direction: column;
      justify-content: space-evenly;
    /* outline: 1px solid black; */
  }
    .imageRowItem{
      display: flex;
        flex-direction: column;
        align-items: center;
      /* max-width: 220px; */
      /* outline: 1px solid red; */
      /* width: 25%; */
    }
      .imageRowItem > img{
        box-shadow: 0px 13px 20px 0px #00000030;
        height: auto; 
        width: 80%;
          max-width: 400px;
        /* width: 180px; */
      }
  
      .imageRowItem > span {
        color: #D1898B;
        font-family: 'Poppins', sans-serif; 
        font-weight: 900;
        margin-top: 30px;
        width: 80%;
      }
      
  .section2 > button{
    color: #D1898B;
    background-color: white;
    cursor: pointer;
    margin-bottom: 20px;
    margin-top: 20px;
    outline-color: #D1898B;
    padding: 5px;
    width: 145px;
  }
  
    .section2 > button:hover{
     -webkit-animation: section2ButtonHover 1s;
             animation: section2ButtonHover 1s;
     -webkit-animation-fill-mode: forwards;
             animation-fill-mode: forwards; 
     color: white;
    }
    @-webkit-keyframes section2ButtonHover {
      0%{background-color: white;}
      50%{background-color: #D1898B;}
      100%{background-color: #D1898B;}
    }
    @keyframes section2ButtonHover {
      0%{background-color: white;}
      50%{background-color: #D1898B;}
      100%{background-color: #D1898B;}
    }
  
  
      @media (min-width: 768px) {
        .imageRow{
          flex-direction: row;
          flex-wrap: wrap;
          margin: 0 auto;
          width: 100%;
        }
        .imageRowItem{
          max-width: 350px;
          width: 100%;
          margin: 0 auto;
          /* animation: animateProduct 3s;
          animation-fill-mode: forwards; */
        }

        @-webkit-keyframes animateProduct {
          0%{
            -webkit-transform: translateY(100px);
                    transform: translateY(100px);
          }
          100%{
            -webkit-transform: translateY(0px);
                    transform: translateY(0px);
          }
        }

        @keyframes animateProduct {
          0%{
            -webkit-transform: translateY(100px);
                    transform: translateY(100px);
          }
          100%{
            -webkit-transform: translateY(0px);
                    transform: translateY(0px);
          }
        }
        .imageAndTextRow.lower{
          flex-direction: row-reverse;
        }
        .imageAndTextRow.upper{
          flex-direction: row;
        }
  
      }
      @media (min-width: 1024px) {
        .imageRowItem{
          /* max-width: 400px; */
          width: 25%;
          margin: 0 auto;
          min-height: 400px;
        }
      }
