    .imageContainer{
      /* background-image: url('./images/interview_image.jpg'); */
  
      /* border-bottom-right-radius: 100%; */
      
      display: block;
      height: 100%;
      margin: 0 auto;
      /* outline: 1px solid black; */
      position: relative;
      width: 100%;
        max-width: 300px;
      
        /* border-radius: 0% 0% 30% 0%/0% 0% 30% 0%;; */
    }
      #interviewImage{
        background-image: url(../../static/media/interview_image.fd88dc9b.jpg);
        background-size: cover;
        background-position: center;
      }
  
      #socialImage{
        background-image: url(../../static/media/social_image.eb5311e7.jpeg);
        background-size: cover;
        background-position: bottom;
      }
    .imageContainer > svg {
      position: absolute;
      bottom: 0;
      left: 0;
      /* transform: rotateY(180deg); */
    }
/* section 4 */
.section4{
    font-size: 13px;
    max-width: 1200px; 
    margin: 0 auto;
    padding-bottom: 70px;
    padding-top: 70px;
  }
  .section4 > *{
    color: black;
  }
  .section4 > img{
    max-width: 230px;
    -webkit-animation: pulseStars 10s;
            animation: pulseStars 10s;
  }
  @-webkit-keyframes pulseStars {
    0%{
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }

    95%{
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
    }
    100%{
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  @keyframes pulseStars {
    0%{
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }

    95%{
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
    }
    100%{
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  
